<template>
  <div>
        <l-map style="height: 350px" :zoom="zoom" :center="[userDetail.data.lat, userDetail.data.lon]">
          <l-tile-layer :url="url"></l-tile-layer>
          <div v-for="(item, index) in allFriends.data" :key="index">
            <l-marker  :lat-lng="[item.lat, item.lon]" >
              <l-popup :content="item.name" ></l-popup>
            </l-marker>
          </div>
          <l-marker :lat-lng="[userDetail.data.lat, userDetail.data.lon]"><l-popup :content="userDetail.data.name" ></l-popup></l-marker>
        </l-map>
        <router-link to="/home">
              <button class="btn">Back to Home</button>
        </router-link>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import L from 'leaflet'
import 'leaflet/dist/leaflet.css'
import { LMap, LTileLayer, LMarker, LPopup } from 'vue2-leaflet'
delete L.Icon.Default.prototype._getIconUrl
L.Icon.Default.imagePath = ''
L.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})
export default {
  components: {
    LMap,
    LTileLayer,
    LMarker,
    LPopup
  },
  data () {
    return {
      username: localStorage.getItem('username'),
      myData: '',
      myLon: '',
      myLat: '',
      friendsData: '',
      url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 11
    }
  },
  computed: {
    ...mapGetters({
      userDetail: 'user/userDetail',
      allFriends: 'user/allFriends'
    })
  },
  methods: {
    ...mapActions({
      actionGetAllFriends: 'user/getAllFriends',
      actionGetMyDetail: 'user/getDetail'
    }),
    getMyDetail () {
      this.actionGetMyDetail()
      this.myData = this.userDetail.data
    }
  },
  mounted () {
    this.getMyDetail()
    this.actionGetMyDetail()
      .then((result) => {
        console.log(result)
        this.friendsData = this.allFriends.data
      })
  }
}
</script>
